body {
  /* font-family: sans-serif; */
  margin: 0;
  background-color: white;
}

a {
  color:white;
  font-weight: 100;
  letter-spacing: 2px;
  text-decoration: none;
  background:rgba(0,0,0,0.2);
  padding:10px 0px 10px 0px;
  display: inline-block;
  width: 100%;
  text-align: center;
  transition:all 0.5s;
}

li:hover {
  background:rgba(0,0,0,0.3);
}

.wrapper {
  /* max-width: 300px; */
  margin: 0 auto;
  padding:2em 2em 0em 2em;
}


ul {
  border: 1px solid black;
  list-style: none;
  margin: 0;
  padding: 0;
  display:flex;
}

#aboutMe {
  margin: 0px 0px 5px 0px;
  border-bottom: solid 1px #4a9ef7;
  /* text-shadow: 1px 1px white; */
  color:#0d47a1;
}

 li {
  flex:3;
  text-shadow: 1px 1px #000000;
}

@media all and (max-width:1000px) {
  
  ul {
    flex-wrap:wrap;
  }
  li {
    flex:1 1 50%;
  }
}

@media all and (max-width:600px) {
  li {
    flex-basis:100%;
  }

  #llame {
    float: none;
  }
  #profileImg {
    float: none;
    display: block;
    justify-self: center;
    display: flex;
    flex-direction: column;
    align-self: center;
    margin: auto;
  }
  .box1 {
    align-content: center;
    align-items: center;
  }
  .projBanner {
    position:absolute;
    bottom: 5%;
    left: 25%;
    background-color: #0d47a1;
    width: 40%;
    border: solid 1px white;
    /* padding: 0px 0px 4px 0px; */
    /* margin-left: 4px; */
    /* overflow: hidden; */
  }
  /* img {
    width: 80%;
  } */
  .wrapper {
    padding: 0px;
  }
}

@media all and (max-width:510px) {
  .projectGrid {
    display:grid;
    justify-content: center;
    justify-items: center;
    grid-template-columns: repeat(auto-fit);
    /* padding: 1em; */
    grid-gap: 1em;
    grid-template-rows: repeat(1, 300px);
  }
}



#profileImg {
  float: left;
  margin: 0px 1em 0px 0px;
  border: 1px solid grey;
  align-self: center;
  justify-self: center;
}

p {
    font-size: 25.2px;
    text-indent: 20px;
    align-content: center;
}


img:hover {
  background:rgba(0,0,0,0.3);
}
  
.blueLink {
  background-color: #4a9ef7;
  border: solid 1px white
}